/* stylelint-disable */
html, body, div, span,
a, small, ol, ul, li, article,
aside, figure, footer, header, hgroup,
menu, nav, section, time {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border: 0;
  box-sizing: border-box;
  font-size: 100%;
  font: inherit;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
html, body {
  font-size: 100%;
}
article, aside, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
h1, h2 {
  margin: 0;
  padding: .5em 0;
}
ol, ul {
  list-style: none;
}
/* stylelint-enable */
body {
  --color: white;
  --bg-color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  margin: 0;
  position: relative;
  text-align: center;
}
.btn {
  display: inline-block;
  color: var(--bg-color);
  cursor: pointer;
  background: transparent;
  border: 1px solid var(--bg-color);
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  font-size: 16px;
  line-height: 1.3;
  padding: 0.55rem 0.75rem;
  text-transform: uppercase;
  text-align: center;
  white-space: nowrap;
}
.btn:hover {
    background-color: rgba(100, 100, 100, 0.3);
}
.layer {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: rgba(0, 0, 0, 0.6);
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}
.layer p {
    color: var(--color);
}
.layer .btn {
    border-color: var(--color);
    color: var(--color);
}
.layer .btn-main {
    background: var(--color);
    color: var(--bg-color);
    border-color: var(--color);
}

.gameboard-container {
  margin: 0 auto;
  max-width: 500px;
}
.gameboard-row {
  display: grid;
  grid-template-columns: repeat(8, 12.5%);
}

.gameboard-square {
  --square-padding: 5%;
  background-color: #23613e;
  -webkit-box-shadow: 0 0 0 1px grey;
          box-shadow: 0 0 0 1px grey;
  padding-top: 100%;
  position: relative;
}
.gameboard-square::after {
    background-color: transparent;
    border-radius: 50%;
    bottom: var(--square-padding);
    content: "";
    display: block;
    left: var(--square-padding);
    position: absolute;
    right: var(--square-padding);
    top: var(--square-padding);
}
.black::after {
  background-color: black;
}
.white::after {
  background-color: white;
}
.hints .playable {
  cursor: pointer;
}
.hints .playable::after {
    background-color: rgba(0, 0, 0, 0.2);
}
